<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>分类参数</el-breadcrumb-item>
    </el-breadcrumb>

    <div class="content">
      <el-alert
        title="注意：只允许为第三级参数设置分类"
        type="warning"
        show-icon
        effect="dark"
      >
      </el-alert>

      <div class="fenlei">
        <p>
          请选择商品分类：
          <el-cascader
            v-model="selsetIdArr"
            :options="categoriesList"
            :props="peizhi"
            clearable
            @change="handleChange"
          ></el-cascader>
        </p>
      </div>

      <el-tabs @tab-click="tabs" type="border-card">
        <el-button type="primary" @click="showAdd" :disabled="isDisabled">{{addwhat}}</el-button>
           
         
        <el-tab-pane label="动态参数">
          <el-table :data="attributes" style="width: 100%">
            <el-table-column type="expand">
              <template >
                <el-form label-position="left" inline class="demo-table-expand">
                </el-form>
              </template>
            </el-table-column>
            <el-table-column label="#" type="index"> </el-table-column>
            <el-table-column label="参数名称" prop="attr_name">
            </el-table-column>
            <el-table-column label="操作" >
              <template slot-scope="scope" >
                <el-button
                  type="primary"
                  @click="edit(scope.row)"
                  icon="el-icon-edit"
                  >修改</el-button
                >
                <el-button
                  type="danger"
                  @click="del(scope.row)"
                  icon="el-icon-delete"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="静态属性">
          <el-table :data="attributes" style="width: 100%">
            <el-table-column type="expand">
              <template >
                <el-form label-position="left" inline class="demo-table-expand">
                </el-form>
              </template>
            </el-table-column>
            <el-table-column label="#" type="index"> </el-table-column>
            <el-table-column label="参数名称" prop="attr_name">
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope" >
                <el-button
                  type="primary"
                  @click="edit(scope.row)"
                  icon="el-icon-edit"
                  >修改</el-button
                >
                <el-button
                  type="danger"
                  @click="del(scope.row)"
                  icon="el-icon-delete"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </div>

      <!-- 编辑属性 -->
      <el-dialog
        :title="title"
        :visible.sync="dongtai"
        @before-close="dongtaiClose"
        width="50%">
         <el-form ref="add" label-width="80px">
            <el-form-item :label="label">
              <el-input v-model="attr_name"></el-input>
            </el-form-item>
          
          </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dongtaiClose">取 消</el-button>
          <el-button type="primary" @click="update"
            >确 定</el-button
          >
        </span>
      </el-dialog>

         <!-- 添加属性-->
      <el-dialog
        :title="addTitle"
        :visible.sync="isShowAdd"
        @before-close="showAdd"
        width="50%">
         <el-form ref="add" label-width="80px">
            <el-form-item :label="label">
              <el-input v-model="addAttr"></el-input>
            </el-form-item>
          </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="showAdd">取 消</el-button>
          <el-button type="primary" @click="add"
            >确 定</el-button
          >
        </span>
      </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      addTitle:"添加动态参数",
    
      addAttr:"",
      isShowAdd:false,
      isDisabled:true,
      addwhat:"添加参数",
      dongtai:false,
      peizhi: {
        label: "cat_name",
        value: "cat_id",
        children: "children",
        expandTrigger: "hover",
      },
      categoriesList: [],
      selsetIdArr: [],
      sel:"many",
      attributes: [],
      updateAttributesObj:{},
      attr_name:"",
      title:"修改动态参数",
      label:"动态参数"
    };
  },
  created() {},
  mounted() {
    this.getcategories();
  },
  methods: {

    // 添加属性
    async add(){

      let {data}= await this.$http.post(`categories/${this.selsetIdArr[2]}/attributes`,{
        attr_name:this.addAttr,
        attr_sel:this.sel
      })

      console.log(data)
      this.getSelList()
      this.isShowAdd=!this.isShowAdd
 this.$message({
          type: "success",
          message: "添加成功!",
        });

    },

    // 显示关闭添加弹框
    showAdd(){
      this.isShowAdd=!this.isShowAdd

    },
    
    

    // 修改属性
   async update(){

     let {data} = await this.$http.put(`categories/${this.updateAttributesObj.cat_id}/attributes/${this.updateAttributesObj.attr_id}`,{
       attr_name:this.attr_name,
       attr_sel:this.sel
     })
      this.dongtai=! this.dongtai
      this.getSelList()
     console.log(data)
 this.$message({
          type: "success",
          message: "修改成功!",
        });
    },

    // 点击修改
    edit(i){

      this.updateAttributesObj=i
      this.dongtai=! this.dongtai


    },


    dongtaiClose(){

      this.dongtai=! this.dongtai

    },

    // 删除属性
   async  del(i){

      let is = await this.$confirm("确定删除么, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      });

      if (is) {
        let {data} = await this.$http.delete(`/categories/${i.cat_id}/attributes/${i.attr_id}`)
        console.log(data);
         this.getSelList();
        this.$message({
          type: "success",
          message: "删除成功!",
        });
      } else {
        this.$message({
          type: "info",
          message: "删除失败",
        });
      }

     
      console.log(data)

    },


    //  tab选项
    tabs(i) {
      console.log(i);

      if (i.panName == 0) {
        this.sel = "many";
        this.title="修改动态参数"
        this.label="动态参数"
        this.addTitle="添加动态参数"
       
        this.addwhat="添加参数"
        this.getSelList();
      } else {
        this.sel = "only";
        this.getSelList();
        this.addTitle="添加静态属性"

          this.title="修改静态属性"
        this.label="静态属性"
        this.addwhat="添加属性"

      }
    },

    // 获取动态参数或静态属性

    async getSelList() {
      let { data } = await this.$http(
        `/categories/${this.selsetIdArr[2]}/attributes/?sel=${this.sel}`
      );
      console.log(data);
      this.attributes = data.data;
    },

    //   获取分类列表
    async getcategories() {
      let { data } = await this.$http.get("/categories", {
        params: { type: 3 },
      });

      console.log(data);
      this.categoriesList = data.data;
    },

    handleChange() {
      console.log(this.selsetIdArr[2]);

      if(this.selsetIdArr.length==0){
         
       this.isDisabled=true

      }else{
       this.isDisabled=false

      }

      if (this.selsetIdArr.length < 3) {
        this.$message({
          type: "error",
          message: "只能选择第三级分类",
        });
      }
      this.getSelList();
    },
  },
};
</script>

<style lang="scss" scoped>
.el-tabs {
  margin-top: 30px;
}
.content {
  width: 100%;
  margin-top: 20px;
  height: 734px;
  background: white;
  padding: 30px;
  > p {
    width: 100%;
    height: 40px;
  }
}
.fenlei {
  margin-top: 30px;
}
</style>
