<template>
  <div class="roles">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>

    <div class="table" >
      <p>
        <el-button
          type="primary"
          @click="addShow = !addShow"
          icon="el-icon-edit"
          >添加角色</el-button
        >
      </p>

      <el-table stripe border :data="list" style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-row
                :class="['dbbottom', index === 0 ? 'dbtop' : '']"
                v-for="(item, index) in scope.row.children"
                :key="index"
              >
                <!-- <el-col :span="24"> -->
                <el-col :span="5">
                  <el-tag
                    closable
                    @close="removeRights(scope.row, item.id)"
                    :disable-transitions="false"
                    effect="dark"
                    >{{ item.authName }}</el-tag
                  >
                  <i class="el-icon-caret-right"></i>
                </el-col>
                <el-col :span="19">
                  <el-row
                    :class="[i === 0 ? '' : 'dbtop']"
                    v-for="(it, i) in item.children"
                    :key="i"
                  >
                    <el-col :span="6">
                      <el-tag
                        closable
                        type="success"
                        @close="removeRights(scope.row.id, it.id)"
                        :disable-transitions="false"
                        effect="dark"
                        >{{ it.authName }}</el-tag
                      >
                      <i class="el-icon-caret-right"></i>
                    </el-col>
                    <el-col :span="18">
                      <el-tag
                        closable
                        type="warning"
                        :disable-transitions="false"
                        @close="removeRights(scope.row.id, t.id)"
                        v-for="(t, d) in it.children"
                        effect="dark"
                        :key="d"
                        >{{ t.authName }}</el-tag
                      >
                    </el-col>
                  </el-row>
                </el-col>
                <!-- </el-col> -->
              </el-row>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column label="ID" prop="id"> </el-table-column>
        <el-table-column label="角色名称" prop="roleName"> </el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"> </el-table-column>
        <el-table-column label="角色操作">
          <template slot-scope="scope">
            <el-button
              type="primary"
              @click="edit(scope.row)"
              icon="el-icon-edit"
              >编辑</el-button
            >
            <el-button
              type="danger"
              @click="del(scope.row)"
              icon="el-icon-delete"
              >删除</el-button
            >
            <el-button
              type="warning"
              @click="set_role(scope.row)"
              icon="el-icon-setting"
              >分配权限</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 修改 -->
    <!-- 修改数据 -->
    <van-popup v-model="updateShow">
      <div class="update">
        <p>
          <span>修改角色</span>
          <span @click="updateShow = !updateShow">x</span>
        </p>
        <div class="input">
          <!-- <el-form-item label="活动名称" prop="name">
                    <el-input></el-input>
                </el-form-item> -->
          <el-form
            :model="editObj"
            :rules="rules"
            ref="updateForm"
            label-width="80px"
          >
            <el-form-item prop="roleName" label="角色名称">
              <el-input v-model="editObj.roleName"></el-input>
            </el-form-item>
            <el-form-item prop="roleDesc" label="角色描述">
              <el-input v-model="editObj.roleDesc"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <div class="addBtn">
          <el-button @click="updateShow = !updateShow">取消</el-button>
          <el-button @click="update" type="primary">确定</el-button>
        </div>
      </div>
    </van-popup>

    <van-popup v-model="addShow">
      <div class="update">
        <p>
          <span>添加角色</span>
          <span @click="addShow = !addShow">x</span>
        </p>
        <div class="input">
          <!-- <el-form-item label="活动名称" prop="name">
                    <el-input></el-input>
                </el-form-item> -->
          <el-form
            :model="addObj"
            :rules="rules"
            ref="addForm"
            label-width="80px"
          >
            <el-form-item prop="roleName" label="角色名称">
              <el-input v-model="addObj.roleName"></el-input>
            </el-form-item>
            <el-form-item prop="roleDesc" label="角色描述">
              <el-input v-model="addObj.roleDesc"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <div class="addBtn">
          <el-button @click="addShow = !addShow">取消</el-button>
          <el-button @click="add" type="primary">确定</el-button>
        </div>
      </div>
    </van-popup>

    <!-- 分配角色 -->
    <van-popup v-model="issetroleShow">
      <div class="set_role" >
          <div class="center">
           
           
             
             <el-tree  node-key="id" ref="tree" :default-checked-keys="checkboxArr" default-expand-all show-checkbox :data="rightsList" :props="treeProps"></el-tree>
        
          <div class="addBtn"> 
          <el-button @click="issetroleShow = !issetroleShow">取消</el-button>
          <el-button @click="setRole()" type="primary">确定</el-button>
          </div>
         
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
       checkboxArr:[],
       setRoleObj:{},
      issetroleShow:false,
      rightsList:[],
       treeProps:{
        children: 'children',
        label: 'authName'
      },
      rules: {
        roleName: [
          {
            required: true,
            message: "请输入名称",
            trigger: ["blur", "change"],
          },
          {
            min: 3,
            max: 9,
            message: "长度在 3 到 9个字符",
            trigger: ["blur", "change"],
          },
        ],
        roleDesc: [
          {
            required: true,
            message: "请输入描述",
            trigger: ["blur", "change"],
          },
          {
            min: 3,
            max: 9,
            message: "长度在 3 到 9个字符",
            trigger: ["blur", "change"],
          },
        ],
      },
      list: [],
      updateShow: false,
      editObj: {
        roleName: "",
        roleDesc: "",
      },
      addObj: {
        roleName: "",
        roleDesc: "",
      },
      addShow: false,
    };
  },
  watch: {
    addShow(val) {
      if (!val) {
        this.addObj = {
          roleName: "",
          roleDesc: "",
        };
      }
    },
  },
  mounted() {
    this.getList();
  },
  methods: {

   

    // 点击分配角色
    set_role(i){
        
       this.digui(i,this.checkboxArr)
       
     console.log(i,this.checkboxArr)
      this.issetroleShow=!this.issetroleShow
      this.setRoleObj=i
      this.$http.get('rights/tree').then((res) => {
        
        this.rightsList=res.data.data
      })

    },
  

  //  确认分配角色
   async setRole(){

    
    let keys=[
      ...this.$refs.tree.getCheckedKeys(),
      ...this.$refs.tree.getHalfCheckedKeys()

    ]
    
  let arr=keys.join(",")   
    
   console.log(arr)
     let {data}= await this.$http.post(`roles/${this.setRoleObj.id}/rights`,{rids:arr})
   
      console.log(data)
      this.issetroleShow=!this.issetroleShow

      this.getList()

    },

    digui(node,arr){

      if(!node.children){
        console.log(node.id)
        return arr.push(node.id)

      }

      node.children.forEach((i)=>{
            
            this.digui(i,arr)

      })

    },
   

    //   删除数据

    async del(i) {
      await this.$http.delete("/roles/" + i.id);

      this.getList();
    },

     //  删除指定权限

    async removeRights(i, id) {
      let is=  await this.$confirm("确定删除么, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
       
       if(is){
            let { data } = await this.$http.delete(`roles/${i}/rights/${id}`)
                console.log(data);
             this.getList()
          this.$message({
                      type: "success",
                      message: "删除成功!",
                    })
       }else{
            this.$message({
                        type: "info",
                        message: "删除失败",
                      });
       }

    },

    //   添加数据
    async add() {
      await this.$http.post("/roles", {
        roleName: this.addObj.roleName,
        roleDesc: this.addObj.roleDesc,
      });
      this.getList();

      this.addShow = !this.addShow;
    },

    // 点击修改显示弹框
    edit(i) {
      this.updateShow = !this.updateShow;
      this.editObj = JSON.parse(JSON.stringify(i));
    },

    // 确认修改
    async update() {
      await this.$http.put(`/roles/${this.editObj.id}`, {
        roleName: this.editObj.roleName,
        roleDesc: this.editObj.roleDesc,
      });

      this.updateShow = !this.updateShow;
      this.getList();
    },

    // 获取列表数据
    getList() {
      this.$http.get("/roles").then(({ data }) => {
        console.log(data);
        this.list = data.data;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.set_role{
  width: 800px;
  background: white;
  position: relative;
  padding: 30px;
  .center{
    padding-bottom: 50px;
  }
   .addBtn {
    width: 200px;
    height: 40px;
    margin-top: 50px;
    position: absolute;
    bottom: 30px;
    right: 30px;
  }
}
.rolesList {
  width: 100%;
  display: flex;
  flex-wrap: wrap;

  border: 1px solid seagreen;

  .el-button {
    width: 100px;
    height: 40px;
    font-size: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    span {
      margin-right: 10px;
    }
  }
}
.hasgutter {
  height: 50px !important;
}
.el-table {
  margin-top: 30px;
  .el-table__header-wrapper {
    height: 40px;
    overflow: hidden;
    background: red !important;
  }
}
.roles {
  width: 100%;
  height: 100%;
}
.update {
  width: 700px;
  height: 300px;
  padding: 20px;
  box-sizing: border-box;
  position: relative;
  > p {
    width: 100%;
    height: 60px;
    display: flex;
    margin-bottom: 20px;
    align-items: center;
    justify-content: space-between;
    > :nth-child(1) {
      font-size: 20px;
    }
  }
  .addBtn {
    width: 200px;
    height: 40px;
    position: absolute;
    bottom: 30px;
    right: 30px;
  }
}
.table {
  margin-top: 30px;
  padding: 20px 20px;
  display: flex;
  flex-direction: column;
  background: white;
  > p {
    width: 100px;
  }
}
</style>
