<template>
  <div
  >
 <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>数据管理</el-breadcrumb-item>
      <el-breadcrumb-item>报表数据</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <div id="myChart" :style="{ width: '800px', height: '400px' }"></div>
    </el-card>
  </div>
</template>


<script>
import echarts from 'echarts'
export default {
  data() {
    return {};
  },
  created() {},
  mounted() {
    let myChart = echarts.init(document.getElementById('myChart'))
    var drawLine = this.drawLine();
    window.onresize = function temp() {
      drawLine.resize(); //重绘，窗口大小改动时
    };
  },
  methods: {
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById('myChart'))
      // 绘制图表
      myChart.setOption({
            title: {
              // text: '堆叠区域图'
              text: '用户来源'
            },
            tooltip: {
                trigger: 'axis',
                axisPointer: {
                    type: 'cross',
                    label: {
                        backgroundColor: '#6a7985'
                    }
                }
            },
            legend: {
                // data: ['邮件营销', '联盟广告', '视频广告', '直接访问', '搜索引擎']
                data: ['华东', '华南', '华北', '西部', '其他']
            },
            toolbox: {
                feature: {
                    saveAsImage: {}
                }
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            xAxis: [
                {
                    type: 'category',
                    boundaryGap: false,
                    data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
                }
            ],
            yAxis: [
                {
                    type: 'value'
                }
            ],
            series: [
                {
                    name: '华东',
                    type: 'line',
                    stack: '总量',
                    areaStyle: {},
                    data: [2999, 3111, 4100, 3565, 3528, 6000, 6528]
                },
                {
                    name: '华南',
                    type: 'line',
                    stack: '总量',
                    areaStyle: {},
                    data: [5090, 2500, 3400, 6000, 6400, 7800, 7000]
                },
                {
                    name: '华北',
                    type: 'line',
                    stack: '总量',
                    areaStyle: {},
                    data: [6888, 4000, 8010, 12222, 13548, 12584, 12500]
                },
                {
                    name: '西部',
                    type: 'line',
                    stack: '总量',
                    areaStyle: {},
                    data: [9991, 4130, 7777, 12603, 12898, 13928, 13320]
                },
                {
                    name: '其他',
                    type: 'line',
                    stack: '总量',
                    label: {
                        normal: {
                            show: true,
                            position: 'top'
                        }
                    },
                    areaStyle: {},
                    data: [15212, 5800, 10241, 14521, 15782, 13891, 14965]
                }
            ]
          });
    　　　　return myChart;
        } 
      } 
};
</script>


<style scoped lang="scss">
#myChart{
  margin-top: 10px;
}
.bg{
  background: white;
}
.el-card{
  margin-top: 30px;
}
</style  >