<template>
  <div class="rights">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>权限列表</el-breadcrumb-item>
    </el-breadcrumb>

    <div class="table">
      <table border="1" cellspacing="0" cellpadding="0">
        <tr>
          <th>#</th>
          <th>权限名称</th>
          <th>路径</th>
          <th>权限等级</th>
        </tr>
        <tr v-for="(i, k) in list" :key="k">
          <td>{{ k }}</td>
          <td>{{ i.authName }}</td>
          <td>{{ i.path }}</td>
          <td>
            <el-button v-show="i.level == 0" type="primary" plain
              >一级</el-button
            >
            <el-button v-show="i.level == 1" type="success" plain
              >二级</el-button
            >
            <el-button v-show="i.level == 2" type="warning" plain
              >三级</el-button
            >
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: "rights",
  data() {
    return {
      list: [],
    };
  },
  created() {},
  mounted() {
    this.getList();
  },
  methods: {
    //  获取权限列表
    getList() {
      this.$http.get("/rights/list").then(({ data }) => {
        console.log(data);
        this.list = data.data;
      });
    },
  },
};
</script>

<style scoped lang="scss">
.rights {
  flex: 1;
}
.table {
  margin-top: 20px;
  width: 100%;
  height: 100%;
  background: white;
  padding: 20px 20px;
}
table {
  width: 90%;
  line-height: 50px;
  margin-left: 30px;
  margin-top: 30px;
  border: 1px solid #eaedf1;
  td {
    border: 1px solid #eaedf1;
    text-align: left;
    padding-left: 20px;
  }
  th {
    border: 1px solid #eaedf1;
  }
}
.el-button {
  padding: 5px 7px;
  font-size: 10px;
  margin-left: 11px;
}
</style>
