<template>
  <div class="goods">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>

    <div class="content">
      <div class="search">
        <input type="text" v-model="keyword" placeholder="请输入搜索内容" />
        <button @click="search()" class="search_btn">
          <van-icon class="search_ico" name="search" />
        </button>
        <el-button
          @click="toAdd"
          style="height: 40px; margin-left: 30px"
          type="primary"
          >添加商品</el-button
        >
      </div>

      <el-table :data="list" border style="width: 100%">
        <el-table-column type="index" label="#" width="48"> </el-table-column>
        <el-table-column prop="goods_name" label="商品名称" width="742">
        </el-table-column>
        <el-table-column prop="goods_price" label="商品价格"> </el-table-column>
        <el-table-column prop="goods_weight" label="商品重量">
        </el-table-column>
        <el-table-column prop="add_time" label="创建时间"> 
            <template slot-scope="props">
                 {{props.row.add_time }}
            </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scoped">
            <el-button
              type="primary"
              @click="update_one(scoped.row)"
              icon="el-icon-edit"
              size="mini"
            ></el-button>
            <el-button
              @click="del(scoped.row)"
              type="danger"
              icon="el-icon-delete"
              size="mini"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagenum"
        :page-sizes="[10, 20, 35, 40, 55, 60, 70, 80, 90, 100]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </div>

    <!-- 修改 -->
    <van-popup v-model="updateShow">
      <div class="update">
        <p>
          <span>修改商品</span>
          <span @click="updateShow = !updateShow">x</span>
        </p>
        <div class="input">
          <el-form ref="updateForm" label-width="80px">
            <el-form-item prop="roleName" label="商品名称">
              <el-input v-model="updateObj.goods_name"></el-input>
            </el-form-item>
            <el-form-item prop="roleDesc" label="商品价格">
              <el-input v-model="updateObj.goods_price"></el-input>
            </el-form-item>
            <el-form-item prop="roleName" label="商品数量">
              <el-input v-model="updateObj.goods_number"></el-input>
            </el-form-item>
            <el-form-item prop="roleDesc" label="商品重量">
              <el-input v-model="updateObj.goods_weight"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <div class="addBtn">
          <el-button @click="updateShow = !updateShow">取消</el-button>
          <el-button @click="update" type="primary">确定</el-button>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      pagenum: 1,
      pagesize: 10,
      total: 0,
      keyword: "",
      issearch: false,
      updateShow: false,
      updateObj: {
        goods_name: "",
        goods_number: "",
        goods_price: "",
        goods_weight: "",
      },
    };
  },

  watch: {
    keyword(val) {
      if (val == "") {
        this.issearch = false;
        this.getList();
      }
    },
  },
  mounted() {
    this.getList();
  },
  methods: {
    //   跳转到添加商品页面
    toAdd() {
      this.$router.push("/home/addGoods");
    },

    // 点击修改
    update_one(i) {
      this.updateShow = !this.updateShow;
      console.log(i);
      this.updateObj = i;
    },

    // 确认修改
    async update() {
      let res = await this.$http.put(`/goods/${this.updateObj.goods_id}`, {
        goods_name: this.updateObj.goods_name,
        goods_price: this.updateObj.goods_price,
        goods_number: this.updateObj.goods_number,
        goods_weigth: this.updateObj.goods_weight,
      });

      console.log(res);
      this.updateShow = !this.updateShow;
    },

    // 获取商品列表
    async getList() {
      let res = "";
      if (this.issearch) {
        res = await this.$http.get(
          `/goods?query=${this.keyword}&pagenum=${this.pagenum}&pagesize=${this.pagesize}`
        );
      } else {
        res = await this.$http.get(
          `/goods?pagenum=${this.pagenum}&pagesize=${this.pagesize}`
        );
      }

      this.list = res.data.data.goods;


      this.list.forEach((res) => {
         
         res.add_time = new Date(res.add_time).toLocaleString()
      });
      console.log(res.data);
      this.total = res.data.data.total;
    },

    // 搜索
    async search() {
      this.issearch = true;

      this.getList();
    },

    // 删除
    async del(i) {
      console.log(i);
      let is = await this.$confirm("确定删除么, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      });

      if (is) {
        let { data } = await this.$http.delete(`goods/${i.goods_id}`);
        console.log(data);
        this.getList();
        this.$message({
          type: "success",
          message: "删除成功!",
        });
      } else {
        this.$message({
          type: "info",
          message: "删除失败",
        });
      }
    },

    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pagesize = val;
      this.getList();
    },

    handleCurrentChange(val) {
      this.pagenum = val;
      this.getList();

      console.log(`当前页: ${val}`);
    },
  },
};
</script>

<style lang="scss" scoped>
.update {
  width: 700px;
  padding: 20px;
  height: 500px;
  box-sizing: border-box;
  position: relative;
  > p {
    width: 100%;
    height: 60px;
    display: flex;
    margin-bottom: 20px;
    align-items: center;
    justify-content: space-between;
    > :nth-child(1) {
      font-size: 20px;
    }
  }
  .addBtn {
    width: 200px;
    height: 40px;
    position: absolute;
    bottom: 30px;
    right: 30px;
  }
}

.el-pagination {
  width: 100%;
  display: flex;

  margin-top: 30px;
}
.el-table {
  .el-button {
    width: 44px;
    height: 28px;
    font-size: 10px;
  }
}
.content {
  width: 100%;

  margin-top: 20px;
  background: white;
  padding: 30px;
}
.goods {
  width: 100%;
  height: 100%;
}
.search {
  width: 80%;
  height: 80px;
  display: flex;

  > input {
    width: 300px;
    height: 40px;
    text-indent: 20px;
    border: 1px solid #eaedf1;
  }
  .search_btn {
    width: 60px;
    height: 40px;
    border: 1px solid #eaedf1;
    text-align: center;
    line-height: 40px;
    font-size: 20px;
  }
}
</style>
