<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>订单管理</el-breadcrumb-item>
      <el-breadcrumb-item>订单列表</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-input
        placeholder="请输入内容"
        v-model="keyword"
        clearable
        class="input-with-select"
       
      >
        <el-button slot="append" @click="search" icon="el-icon-search"></el-button>
      </el-input>

      <el-table :data="list" border style="width: 100%">
        <el-table-column type="index" label="#" width="48"> </el-table-column>
        <el-table-column prop="order_number" label="订单编号">
        </el-table-column>
        <el-table-column prop="order_price" label="订单价格价格"> </el-table-column>
        <el-table-column prop="goods_weight" label="是否付款">
            <template slot-scope="scoped">
               <el-button size="mini" v-if="scoped.pay_status===0" type="success">已付</el-button>
               <el-button size="mini" v-else type="danger">未付款</el-button>
            </template>
        </el-table-column>
        <el-table-column prop="is_send" label="是否发货">
         
        </el-table-column>
          <el-table-column prop="create_time" label="下单时间">
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scoped">
            <el-button
              type="primary"
              @click="update_one(scoped.row)"
              icon="el-icon-edit"
              size="mini"
            ></el-button>
            <el-button
              @click="location(scoped.row)"
              type="success"
              icon="el-icon-location"
              size="mini"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

  <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagenum"
        :page-sizes="[10, 20, 35, 40, 55, 60, 70, 80, 90, 100]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>

    </el-card>
      
      
      <el-dialog
        title="添加"
        :visible.sync="xinxi"
        width="50%"
        :before-close="close"
      >
        <el-timeline>
            <el-timeline-item
            v-for="(activity, index) in xinxiList"
            :key="index"
            :icon="activity.icon"
            :type="activity.type"
            :color="activity.color"
            :size="activity.size"
            :timestamp="activity.time">
            {{activity.context}}
            </el-timeline-item>
        </el-timeline>
        <span slot="footer" class="dialog-footer">
          <el-button @click="close">取 消</el-button>
          <el-button type="primary" @click="close"
            >确 定</el-button
          >
        </span>
      </el-dialog>

  </div>
</template>

<script>
export default {
  data() {
    return {
        xinxi:false,
      pagenum: 1,
      pagesize: 10,
      list: [],
      total: 0,
      keyword: "",
      isSearch:false,
      xinxiList:[]
    };
  },
  watch:{
      keyword(val){
          if(val==""){
               this.isSearch=true
        this.getList()    
          }
      }
  },
  mounted() {
    this.getList();
  },
  methods: {

    //   物流信息
   async location(i){

       let {data}= await this.$http.get(`/kuaidi/1106975712662`)
       console.log(data)

        this.xinxi=!this.xinxi
       this.xinxiList=data.data

    },

    close(){

        this.xinxi=!this.xinxi

    },

    //   搜索
    search(){

        this.isSearch=true
        this.getList()

    },


    //    获取列表数据

    async getList() {
            
            let res=""
            console.log(this.keyword)
        if(this.isSearch){
                  res = await this.$http.get(
                `/orders?query=${this.keyword}&pagenum=${this.pagenum}&pagesize=${this.pagesize}`
            );
        }else{
              res = await this.$http(
                `/orders?pagenum=${this.pagenum}&pagesize=${this.pagesize}`
            );
        }
      this.list = res.data.data.goods;
      console.log(this.list)
        this.list.forEach((res) => {
         
         res.create_time = new Date(res.create_time).toLocaleString()
      });
      this.total = res.data.data.total;
    },

      // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pagesize = val;
      this.getList();
    },

    handleCurrentChange(val) {
      this.pagenum = val;
      this.getList();

      console.log(`当前页: ${val}`);
    },
  },
};
</script>

<style scoped>
.el-card {
  margin-top: 30px;
}
.el-input{
    width: 500px;
    margin-bottom: 30px;
}
</style>
