<template>
  <div class="home">
    <el-container>
      <el-header>
        <div class="header_left">
           <img src="../../assets/3.jpg" alt="">
         <span>电商后台管理系统</span>
        </div>
       
        <el-button type="info" @click="open">退出</el-button>
      </el-header>
      <el-container>
        <el-aside width="">
          <!-- <el-col width="200px" :span="12"> -->

          <el-menu
            style="background-color: #333744"
            :default-active="activePath"
            class="el-menu-vertical-demo"
            :collapse="flag"
            @open="handleOpen"
            @close="handleClose"
            unique-opened
            collapse-transition:false
            router
            background-color="#333744"
            text-color="#fff"
            active-text-color="#ffd04b"
          >
            <p class="mulu" @click="flag = !flag">|||</p>

            <el-submenu
              v-for="(i, k) in menus"
              style="background: #333744"
              :index="i.path"
              :key="i.id"
            >
              <template style="background: #333744" slot="title">
                <i :class="ico[k]"></i>
                <span style="margin-right: 100px">{{ i.authName }}</span>
              </template>
                <el-menu-item
                  style="min-width: 190px"
                  v-for="item in i.children"
                  @click="toPath(k, '/home/' + item.path)"
                  :key="item.id"
                  :index="'/home/' + item.path"
                  ><i class="el-icon-menu"></i>
                  {{ item.authName }}</el-menu-item
                >
             
            </el-submenu>
          </el-menu>

          <!-- </el-col> -->
        </el-aside>
        <el-main>
          <keep-alive>
            <router-view></router-view>
          </keep-alive>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
     
      flag: false,
      menus: [],
      ico: [
        "el-icon-user",
        "el-icon-s-tools",
        "el-icon-s-order",
        "el-icon-tickets",
        "el-icon-s-marketing",
      ],
      activePath: sessionStorage.getItem("activePath") || "",
    };
  },
  watch: {
    $route: {
      handler(val) {
        console.log(val);
        sessionStorage.setItem("activePath", val.path);
        this.activePath = sessionStorage.getItem("activePath");
      },
      deep: true,
      immediate: true,
      isClose: false,
    },
  },
  mounted() {
    console.log(JSON.parse(localStorage.getItem("islogin")));
    if (JSON.parse(localStorage.getItem("islogin"))) {
      this.$message({
        type: "success",
        message: "登陆成功",
      });
      localStorage.setItem("islogin", false);
    }

    this.$http("/menus").then((res) => {
      console.log(res.data.data);
      this.menus = res.data.data;
    });
  },
  beforeRouteUpdate(to, from, next) {
    console.log(to, from);

    if (from.path == "/") {
      // alert("555")
    }

    next();
  },
  methods: {
    toPath(k, path) {
      sessionStorage.setItem("activePath", path);
    },

    handleOpen(a, b) {
      console.log(a, b);
      this.opends = b;
    },
    handleClose(a, b) {
      console.log(b);
    },
    //  退出

    open() {
      this.$confirm("确定退出么, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          sessionStorage.removeItem("token");
          this.$router.push("/login");
          this.$message({
            type: "success",
            message: "退出成功!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消退出",
          });
        });
      // }
    },
  },
};
</script>

<style lang="scss" scoped>
.header_left{
  // width: 300px;
  height: 100%;
  display: flex;
  align-items: center;
  
  img{
    width: 100px;
    height: 40px;
    margin-right: 20px;
  }
}
.submenu_title {
  background: #333744;
}
.home {
  width: 100%;
  height: 100%;
}
.el-header {
  background: #373d41;
  // text-indent: 50px;
  /* line-height: 150px; */
  align-items: center;
  display: flex;
  font-size: 24px;
  color: white;
  background: black;
  justify-content: space-between;
}

.el-aside {
  background: #333744;
  color: #333;
  text-align: center;
  box-sizing: border-box;
  height: 100%;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  
  display: flex;
  flex-direction: column;
}

.el-container {
  /* margin-bottom: 40px; */
  height: 100%;
}
.mulu {
  /* width: 200px; */
  height: 40px;
  background: white;
  justify-content: center;
  align-items: center;
  display: flex;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
.out {
  width: 500px;
  height: 200px;
  padding: 20px;
  box-sizing: border-box;
  > p {
    width: 100%;
    height: 60px;
    display: flex;
    // margin-bottom: 20px;
    align-items: center;
    justify-content: space-between;
    > :nth-child(1) {
      font-size: 20px;
    }
  }

  .addBtn {
    width: 200px;
    height: 60px;
    margin-left: 279px;
    // margin-top: 20px;
  }
}
</style>
