<template>
  <div class="addGoods">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>

    <div class="content">
      <el-alert
        title="添加商品信息"
        type="info"
        center
        :closable="false"
        close-text
        show-icon
      >
      </el-alert>

      <div class="buzhou">
        <el-steps
          :active="active"
          finish-status="success"
          :space="200"
          align-center
        >
          <el-step title="基本信息"></el-step>
          <el-step title="商品参数"></el-step>
          <el-step title="商品属性"></el-step>
          <el-step title="商品图片"></el-step>
          <el-step title="商品内容"></el-step>
          <el-step title="完成"></el-step>
        </el-steps>

        <el-tabs :tab-position="tabPosition" @tab-click="activeNum">
          <el-tab-pane label="基本信息">
            <el-form
              :model="addGoods"
              ref="addGoods"
              :rules="rules"
              :label-position="labelPosition"
              label-width="80px"
            >
              <el-form-item prop="goods_name" label="商品名称">
                <el-input v-model="addGoods.goods_name"></el-input>
              </el-form-item>
              <el-form-item prop="goods_price" label="商品区价格">
                <el-input v-model="addGoods.goods_price"></el-input>
              </el-form-item>
              <el-form-item prop="goods_number" label="商品数量">
                <el-input v-model="addGoods.goods_number"></el-input>
              </el-form-item>
              <el-form-item prop="goods_weight" label="商品重量">
                <el-input v-model="addGoods.goods_weight"></el-input>
              </el-form-item>
              <el-form-item prop="goods_weight" label="商品分类">
                  <el-cascader
                    v-model="addGoods.goods_cat"
                    :options="categoriesList"
                    :props="peizhi"
                    clearable
                  ></el-cascader>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="商品参数">配置管理</el-tab-pane>
          <el-tab-pane label="商品属性">角色管理</el-tab-pane>
          <el-tab-pane label="商品图片">定时任务补偿</el-tab-pane>
          <el-tab-pane label="商品内容">
            <div class="textarea">
               <textarea  name="" id="" cols="30" rows="10"></textarea>
            <el-button style="margin-top:20px;" @click="add" type="primary">立即添加</el-button>
            </div>
           
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
    
      categoriesList:[],
       peizhi: {
        label: "cat_name",
        value: "cat_id",
        children: "children",
        expandTrigger: "hover",
      },
      active: 1,
      tabPosition: "left",
      labelPosition: "top",
      rules: {
        goods_name: [
          { required: true, message: "请输入商品名", trigger: "blur" },
        ],
        goods_price: [
          { required: true, message: "请输入价格", trigger: "blur" },
        ],
        goods_weight: [
          { required: true, message: "请输入重量", trigger: "blur" },
        ],
        goods_number: [
          { required: true, message: "请输入数量", trigger: "blur" },
        ],
        goods_fenlei: [
          { required: true, message: "请输选择分类", trigger: "blur" },
        ],
      },
      addGoods: {
        goods_name: "",
        goods_number: "",
        goods_price: "",
        goods_weight: "",
        goods_introduce:"",
        goods_cat:[]
      },
    };
  },
  created() {},
  mounted( ){
    this.getcategories()
  },
  
  methods: {

    // 添加商品
   async add(){
     this.addGoods.goods_cat=this.addGoods.goods_cat.join(",")

     let {data}=await this.$http.post("/goods",this.addGoods)

     this.$router.push("/home/goods")
     console.log(data)

    },

    // 获取分类列表
    async getcategories() {
      let { data } = await this.$http.get("/categories", {
        params: { type: 3 },
      });

      console.log(data);
      this.categoriesList = data.data;
    },

    // 第几步
    activeNum(i) {
      this.active = i.paneName;
      console.log(this.active);
    },
  },
};
</script>

<style lang="scss" scoped>
.textarea{

  display: flex;
  flex-direction: column;

}
.el-tabs {
  margin-top: 30px;
}
.el-tab-pane {
  width: 100%;

  height: 1000px;
}
.content {
  width: 100%;
  margin-top: 20px;
  height: 1000px;
  background: white;
  padding: 30px;
  > p {
    width: 100%;
    height: 40px;
  }
}
.buzhou {
  width: 100%;

  margin-top: 30px;
}
</style>
