<template>
  <div class="login">
        <div class="from">
            <el-form  ref="ruleForm" label-width="300px" class="demo-ruleForm">
                    <el-input type="text" v-model="username" prefix-icon='el-icon-user' placeholder="请输入用户名"></el-input>
            </el-form>
            <el-form  ref="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-input type="password" v-model="password" prefix-icon='el-icon-lock' placeholder="请输入密码"></el-input>
            </el-form>
            <div class="btn">
               <el-button @click="login" type="primary">登录</el-button>
            <el-button @click="clear" type="success">重置</el-button>
            </div>
           
        </div>
        
        <div class="img_vue">
            <img src="../assets/logo.png" alt="">
        </div>
    </div>
</template>

<script>
import axios from "axios"
export default {
  data() {
    return {
        username:'',
        password:""
    };
  },
  created() {},
  mounted() {},
  methods: {
      
      clear(){
           this.username=""
           this.password=""

      },
      login(){

          let obj={
              username:this.username,
              password:this.password
          }
            

          this.$http.post("/login",obj).then(({data})=>{
              // console.log(data.data.token,data.meta.msg)
               if(data.meta.msg=="登录成功"){
                    
                    sessionStorage.setItem("token",data.data.token)

                    this.$router.push("/home")
              }else{
                alert(data.meta.msg)
              }
          })
     

      }
  }
};
</script>

<style scoped lang="scss">
.el-input{
  width: 300px !important;
  height: 50px !important;

}
.btn{
  margin-right: 10px;
  margin-left: 120px;
}
.img_vue{
    width: 130px;
    height: 130px;
    background: #eee;
    position: absolute;
     display: flex;
  justify-content: center;
  align-items: center;
    top: 45%;
    left: 50%;
    padding: 5px 0px;
    box-sizing: border-box;
    transform: translate(-62px,-200px);
    border-radius: 50%;
    border: 10px solid #FFFFFF;
    box-shadow: 0px 0px 10px 5px #ddd;
    overflow: hidden;
    img{
        width: 100px;
        height: 100px;
    }
}
.login {
  width: 100%;
  height: 100%;
  background: #2b4b6b;
  display: flex;
  justify-content: center;
  align-items: center;
}
.from{
    width: 500px;
