import Vue from 'vue'
import VueRouter from 'vue-router'
import login from "@/components/login"
import home from "@/components/index/home"
import home_home from "@/components/index/home_home"
import users from "@/components/user/users"
import rights from "@/components/rights/rights"
import roles from "@/components/rights/roles"
import goods from "@/components/goods/goods"
import addGoods from "@/components/goods/addGoods"
import params from "@/components/goods/params"
import categories from "@/components/goods/categories"
import orders from "@/components/orders/orders"
import reports from "@/components/reports/reports"

Vue.use(VueRouter)

const routes = [
  
  {
   path:"/",
   redirect:'/login'
  },
  {
    path:"/login",
    name:"login",
    component:login,
    beforeEnter(from,to,next){
       console.log(to)
      if(to.path=="/home/home_home"){

        localStorage.setItem("islogin",true)
       
      }

      next()
    }
  },
  {
    path:"/home",
    name:'home',
    component:home,
    redirect:'/home/home_home',
    children:[

      {
        path:'home_home',
        name:'home_home',
        component:home_home
      },
      {
        path:"users",
        name:'users',
        component:users
      },
      {
        path:"rights",
        name:'rights',
        component:rights
      },
      {
        path:"roles",
        name:'roles',
        component:roles
      },
      {
        path:"goods",
        name:'goods',
        component:goods
      },
      {
        path:"params",
        name:'params',
        component:params
      },
      {
        path:"categories",
        name:'categoriess',
        component:categories
      },
      {
        path:"orders",
        name:'orders',
        component:orders
      },
      {
        path:"reports",
        name:'reports',
        component:reports
      },
      {
        path:"addGoods",
        name:'addFoods',
        component:addGoods
      },
    ],
    beforeEnter(to,from,next){
      
      let token= sessionStorage.getItem("token")
        if(!token){
            alert("请先登录")
            next("/login")
           
        }else{
          next()
          // alert("请先登录")
        }

        

    }
  }
 
]

const router = new VueRouter({
  routes
})

export default router
